@import "./utility.styles.scss";

.conf-card {
  width: 28rem;
  height: 36.1rem;
  padding: 0 0 0.8rem;
  border-radius: 0.4rem;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.08);
  background-color: $colour-white;
}

.conf-img-container {
  width: 100%;
  height: 161px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.confcard-body {
  padding-top: 16px;
}

.conf-card-icons {
  width: 1.4rem;
  height: 1.4rem;
}

.conf-card-text {
  display: inline-block;
}

.conf-card-location {
  margin-top: 0.8rem;
  margin-bottom: 0.8rem;
}

.conf-price-box {
  margin-top: 2.5rem;
  margin-left: 1.2rem;
}

.conf-likeicon {
  width: 2.4rem;
  height: 2.4rem;
  position: relative;
  // top: 0.6rem;
  left: 4rem;
}

.card-grid-container {
  display: grid;
  grid-template-columns: 70% 30%;
  position: relative;
  bottom: 0rem;
}
