@import "../../utility/utility.styles.scss";

.navbar {
  width: 100%;

  height: 6.4rem;
  margin: 0 auto;
  display: flex;
  align-items: center;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.12);
  background-color: $colour-white;
  z-index: 99;
  position: sticky;
  // position: fixed;
  top: 0;


  // div:last-child {
  //   margin-left: auto;
  //   margin-right: 2.4rem;
  //   text-align: right;
  // }

  ul {
    display: flex;
    align-items: center;
    list-style: none;

    a {
      text-decoration: none;
      margin-left: 3.2rem;
    }
  }
}

.navbar-first-container {
  display: flex;
  justify-content: center;
}

.navbar-second-container {
  height: 100%;
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: flex-end;

  &>div {
    margin-right: 3.2rem;
  }
}

.logo {
  // width: 11.3rem;
  height: 3.6rem;
  vertical-align: middle;
  margin: 1.4rem 0;
}

.logo-container {
  display: flex;
  align-items: center;
  height: 100%;
  margin-left: 2rem;
  
  

}

.create-conference {
  font-family: Avenir;
  font-size: 1.6rem;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $accent;

}

.signin {
  // margin: 14px 32px;
  font-family: Avenir;
  font-size: 1.6rem;
  font-weight: 900;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $primary;
}

.location {
  display: flex;
  font-family: Avenir-Roman;
  font-size: 1.6rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $primary;
  margin-left: 1.2rem;
  border-left: 1px solid #00000033;
  padding-left: 1.2rem;
  // #000000 is $confemy-black 33 => 0.2 alpha
}

.location-nav {
  display: flex;
  font-family: Avenir-Roman;
  font-size: 1.6rem;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $primary;
  justify-content: flex-start;
  // padding-left: 1.2rem;
  // #000000 is $confemy-black 33 => 0.2 alpha
}

.location-text {
  margin-left: 1.2rem;
  font-family: Avenir-Roman;
  font-size: 1.6rem;

  &-drop{
    margin-left:0;
    margin-right: 1.2rem;

  }
}

.nav-location-icon {
  width: 1.6rem;
  height: 2rem;
}

.search-box {
  align-self: center;
  align-content: center;
  width: 100%;
  margin: 0rem 1rem 0 4rem;
  padding: 0.7rem 1rem;
  border-radius: 4px;
  background-color: $shade-2;
}

.explore-conferences {
  margin-left: 1.2rem;
  width: 100%;
  font-family: Avenir-Roman;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $gray-2;

  &-drop{
    margin-left: 0;
    margin-right: 1.2rem;
    font-family: Avenir-Roman;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: 0.01px;
  color: $gray-2;
  }
}

.user-name-wrapper {
  display: flex;
  align-items: center;
  margin-top: .6rem;

  justify-content:flex-start &:hover {
    cursor: pointer;
  }


  svg {
    margin-left: 0.6rem;
  }
}

