// using material UI breakpoints for differnet screen sizes.

@mixin small() {
  @media only screen and (max-width: 600px) {
    @content;
  }
}

@mixin medium() {
  @media only screen and (min-width: 601px) and (max-width: 960px) {
    @content;
  }
}

@mixin large() {
  @media only screen and (min-width: 961px) and (max-width: 1280px) {
    @content;
  }
}

@mixin xlarge {
  @media only screen and (min-width: 1281px) and (max-width: 1920px) {
    @content;
  }
}

@mixin xxlarge {
  @media only screen and (min-width: 1921px) {
    @content;
  }
}



.navbar-responsive {
  display: grid;
  grid-template-columns: 1fr 1fr .2fr;

  width: 100%;
  max-width: $containerWidth;

  height: 6.4rem;
  position: fixed;
  background-color: $colour-white;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.12);
  align-items: center;

  z-index: 99;

}

.navbar-left-item {

  display: flex;
  align-items: center;
  justify-content: left;

}

.navbar-mid-item {

  display: flex;
  align-items: center;
  justify-content: flex-end;



}

.navbar-right-item {

  display: flex;


}

.auth-cart {
  display: none;
}


.location-flex {
  display: flex;

  justify-content: flex-start;
  align-items: center;
  margin-right: 2rem;
  margin-top: 7px;
}



.hamburger-button {
  display: none;
  justify-content: end;
  align-items: center;
  margin-top: .6rem;
  margin-left: 1rem;

  button {
    background-color: $colour-white;
    border: none;
  }
}

.nav-menu {
  width: 30rem;
  // height: 100%;
  position: fixed;
  z-index: 1;
  overflow: auto;
  top: 6.4rem;
  right: 0;
  border-radius: 4px;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.1);
  background-color: #fafbfc;

  text-align: left;

  @media screen and (min-width: 1000px) {
    display: none;
  }

  // @media screen and (max-width: 600px) {
  //   width: 100%;
  //   height: 100%;

  // }




}

.auth-dropdown-small {
  width: 32.4rem;
  // height: c);
  position: absolute;
  z-index: 1;
  overflow-y: scroll ;
  top: 6.4rem;
  right: 0;
  border-radius: 4px;
  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.08);
  background-color: $colour-white;
  

  ul {

    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;

    li {
      position: relative;
      flex-grow: 1;
      display: flex;
      align-items: center;
      width: 100%;
      min-height: 6.6rem;

      a {
        @include body-regular-a-gray3;
      }

      svg {
        position: relative;
        top: 0.2rem;
        margin-left: 2.4rem;
        margin-right: 2.4rem;
      }

      &:hover {
        background-color: $shade-2;
      }
    }
  }
}




.cart-small {
  margin-top: 2rem;
  height: 100%;

  // position: fixed;
  // top:6.5rem;

  background-color: $colour-white;
  z-index: 1;
  overflow: auto;


  width: 100%;

  right: 0;
  padding: 2rem;
  border-radius: 1rem;

  box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.08);
}

.nav-item {
  padding: 2rem;

  margin: 1rem 2rem;
}

.nav-item:hover {
  background-color: rgba(232, 233, 233, 0.55);
  border-radius: 4px;
}

.close-org-dash{
  display: none;
  align-items: center;

  background-color: #fff;
 
  
  width: 270px;
  padding: 2rem 2.5rem;
  border: 1px solid rgba(164, 164, 164, 0.327);

  
  
  p{
    font-size: 16px;
    
  }
}

@media screen and (max-width: 1000px) {

  .close-org-dash{
    display: flex;
  }

  .cart-text-res {
    display: none;
  }

  .auth-cart {
    display: flex;

  }

  .navbar-mid-item {
    display: none;

  }

  .navbar-right-item {
    justify-content: flex-end;
  }

  .location-flex {
    display: none;
  }

  .navbar-responsive {
    grid-template-columns: auto auto auto;
  }

  .hamburger-button {
    display: flex;


    margin-right: 3rem;
  }

  .search-button-nav {
    display: none;
  }

}

@media screen and (max-width:413px){
  

    .hamburger-button {
   
  
  
      margin-right: 2rem;
    }

}